// composant pour gÃ©nÃ©rer la galerie de la page d'accueil

import { graphql, useStaticQuery } from "gatsby"
import React from "react"
import ImageWrapper from "../components/landingImageWrapper"

import "../styles/components//landingImageGallery.scss"

const ImageGallery = () => {

  const queryOeuvres = useStaticQuery(graphql`
  query oeuvres {
    allWpOeuvre(limit: 10, sort: {order: DESC, fields: date}) {
      nodes {
        custom {
          mainimage {
            localFile {
              childImageSharp {
                fluid(maxWidth: 1000, quality: 100) {
                  ...GatsbyImageSharpFluid_tracedSVG
                }
              }
            }
            id
          }
          bgColor
        }
        id
        title
        slug
      }
    }
  }`)

  return (
    <main>
      {

        // pour chaque oeuvre...
        queryOeuvres.allWpOeuvre.nodes.map(({ id, title, slug, custom }) => {

          // crÃ©Ã© un wrapper pour l'image pour rÃ©duire la longueur
          const fluid = custom.mainimage.localFile.childImageSharp.fluid;
          const alt = custom.mainImage?.altText || ""

          return (
            <ImageWrapper key={id} src={fluid} alt={alt} title={title} slug={slug} bgColor={custom.bgColor}></ImageWrapper>
          )

        }

        )
      }
    </main>
  )

}

export default ImageGallery