import React from "react"
import AnimatedText from "./animatedText"
import "../styles/components/footer.scss"

const Footer = () => {
    return (
        <footer>

            <div>

                <div>
                    <AnimatedText shouldRotate={true} text="MARVIN SCHWAIBOLD"></AnimatedText>
                    <AnimatedText shouldRotate={true} text="LOWER EAST SIDE, NYC"></AnimatedText>
                </div>

                <div>
                    <AnimatedText shouldRotate={true} text="MARVINSCHWAIBOLD@"></AnimatedText>
                    <AnimatedText shouldRotate={true} text="GMAIL.COM"></AnimatedText>
                </div>

                <AnimatedText shouldRotate={true} text="ALL RIGHTS RESERVED 2021"></AnimatedText>

            </div>

        </footer>
    )
}

export default Footer