// composant utilisÃ© dans la page d'accueil pour display chaque projet

import React from "react"
import Image from "gatsby-image"
import AnimatedText from "../components/animatedText"

import "../styles/components/landingImage.scss"


export default class ImageWrapper extends React.Component {

    constructor(props) {
        super(props)
        // le titre vient de wordpress, deux espace signigie un retour Ã  la ligne, on l'Ã©clate selon cette rÃ¨gle en diffÃ©rent span
        this.labelArray = props.title.split("  ");
        this.state = { isHovered: false }
    }

    render() {
        return (
            <div onClick={this.open} onMouseEnter={this.setHover} onMouseLeave={this.setNotHover} className="imageWrapper closed">
                <Image fluid={this.props.src} alt={this.props.alt} />
                <div className="parent">
                    {
                        this.labelArray.map((el, index) => {
                            return (
                                <AnimatedText isHovered={this.state.isHovered} className="test" shouldRotate={true} key={index} text={el}></AnimatedText>
                            )
                        })
                    }
                </div>
            </div>
        )
    }

    setHover = () => {
        this.setState({ isHovered: true })
    }

    setNotHover = () => {
        this.setState({ isHovered: false })
    }

    open = () => {
        console.log("open")
        // rÃ©cupÃ¨re la div pour la transition
        const volet = document.querySelector("div.backgroundTransition")
        // change la couleur du volet
        volet.style.backgroundColor = this.props.bgColor
        // ajoute la class pour animer
        volet.classList.add("animated")
        // la durÃ©e de l'animation est de 400ms, aprÃ¨s ce dÃ©lai on ouvre la nouvelle page
        window.setTimeout(()=>{
            window.location = "/" + this.props.slug
        }, 400)
    }

}
