import React from "react"
import AnimatedText from "./animatedText"
import { Link } from "gatsby"

import "../styles/components//topMenu.scss"

const TopMenu = () => {
  return (
    <div>

      <div className="mainMenu">
        <div className="menuBackground"></div>
        <nav>
          <Link to="/"><AnimatedText shouldRotate={true} text="INTRO/"></AnimatedText></Link>
          <Link to="/allwork"><AnimatedText shouldRotate={true} text="ALL WORK/"></AnimatedText></Link>
          <Link to="/about"><AnimatedText shouldRotate={true} text="ABOUT/"></AnimatedText></Link>
        </nav>
        <AnimatedText text="MARVINSCHWAIBOLD@GMAIL.COM"></AnimatedText>
      </div>

      <nav className="topMenu">
        <svg data-v-29ee60c2="" width="17" height="17" viewBox="0 0 17 17" xmlns="http://www.w3.org/2000/svg" style={{ fill: "#000000" }}><path data-v-29ee60c2="" fillRule="evenodd" clipRule="evenodd" d="M6.9226 0L8.49996 8.49999L3.60495 1.37419L1.37416 3.60497L8.49996 8.49999L0 6.9226V10.0774L8.49996 8.49999L1.37424 13.395L3.60502 15.6258L8.49996 8.49999L6.9226 17H10.0774L8.49996 8.49999L13.3951 15.6258L15.6258 13.395L8.49996 8.49999L17 10.0774V6.9226L8.49996 8.49999L15.6258 3.605L13.395 1.37421L8.49996 8.49999L10.0774 0H6.9226Z"></path></svg>
        <div onClick={toggleMenu} className="menuIcon">
          <div></div>
          <div></div>
        </div>
      </nav>

    </div>
  )

}

const toggleMenu = () => {

  // si le menu Ã©tait ouvert
  if (document.querySelector(".menuIcon").classList.contains("opened")) {

    // les oeuvres de l'accueil
    let oeuvres = document.querySelectorAll(".imageWrapper");
    (oeuvres !== null) ? oeuvres.forEach((oeuvre) => {
      oeuvre.classList.remove("closed")
    }) : console.log("nope");

    // les titres
    let titres = document.querySelectorAll("header .animatedText, footer .animatedText");
    (titres !== null) ? titres.forEach((titre) => {
      titre.classList.add("animated")
    }) : console.log("nope");

    // l'icone du menu
    let menuIcon = document.querySelector(".menuIcon");
    (menuIcon !== null) ? menuIcon.classList.remove("opened") : console.log("nope");

    // les barres du sous titre
    let subTitles = document.querySelector(".subTitle");
    (subTitles !== null) ? subTitles.classList.add("animated") : console.log("nope");

    // le menu
    let menu = document.querySelectorAll(".mainMenu, .mainMenu .animatedText");
    (menu !== null) ? menu.forEach((text) => {
      text.classList.remove("animated")
    }) : console.log("nope")
    let menuBg = document.querySelector("div.menuBackground");
    (menuBg !== null) ? menuBg.style.display = "none" : console.log("nope");

    // la liste les oeuvre de allworks
    let alloeuvres = document.querySelectorAll(".carousel .animatedText");
    (alloeuvres !== null) ? alloeuvres.forEach((oeuvre) => {
      oeuvre.classList.add("animated")
    }) : console.log("nope");

    // les barres dans allworks
    let allWorksBarres = document.querySelectorAll(".carousel > div");
    (allWorksBarres !== null) ? allWorksBarres.forEach((barre) => {
      barre.classList.remove("animated")
    }) : console.log("nope");

    // les animatedTexts dans about
    let aboutTexts = document.querySelectorAll(".about .animatedText");
    (aboutTexts !== null) ? aboutTexts.forEach((text) => {
      text.classList.add("animated");
    }) : console.log("nope");

    // les barres dans about
    let aboutBarres = document.querySelectorAll(".about p");
    (aboutBarres !== null) ? aboutBarres.forEach((barre) => {
      barre.classList.remove("animated");
    }) : console.log("nope");

  } else {
    // si le menu etait fermÃ©

    // les oeuvres
    let oeuvres = document.querySelectorAll(".imageWrapper");
    (oeuvres !== null) ? oeuvres.forEach((oeuvre) => {
      oeuvre.classList.add("closed")
    }) : console.log("nope");

    // les titres
    let titres = document.querySelectorAll("header .animatedText, footer .animatedText");
    (titres !== null) ? titres.forEach((titre) => {
      titre.classList.remove("animated")
    }) : console.log("nope");

    // l'icone du menu
    let menuIcon = document.querySelector(".menuIcon");
    (menuIcon !== null) ? menuIcon.classList.add("opened") : console.log("nope");

    // les barres du sous titre
    let subTitles = document.querySelector(".subTitle");
    (subTitles !== null) ? subTitles.classList.remove("animated") : console.log("nope");

    // le menu
    let menu = document.querySelectorAll(".mainMenu, .mainMenu .animatedText");
    (menu !== null) ? menu.forEach((text) => {
      text.classList.add("animated")
    }) : console.log("nope")
    let menuBg = document.querySelector("div.menuBackground");
    (menuBg !== null) ? menuBg.style.display = "block" : console.log("nope")

    // la liste les oeuvre de allworks
    let alloeuvres = document.querySelectorAll(".carousel .animatedText");
    (alloeuvres !== null) ? alloeuvres.forEach((oeuvre) => {
      oeuvre.classList.remove("animated")
    }) : console.log("nope");

    // les barres dans allworks
    let allWorksBarres = document.querySelectorAll(".carousel > div");
    (allWorksBarres !== null) ? allWorksBarres.forEach((barre) => {
      barre.classList.add("animated")
    }) : console.log("nope");

    // les animatedTexts dans about
    let aboutTexts = document.querySelectorAll(".about .animatedText");
    (aboutTexts !== null) ? aboutTexts.forEach((text) => {
      text.classList.remove("animated");
    }) : console.log("nope");

    // les barres dans about
    let aboutBarres = document.querySelectorAll(".about p");
    (aboutBarres !== null) ? aboutBarres.forEach((barre) => {
      barre.classList.add("animated");
    }) : console.log("nope");

  }

}

export default TopMenu