// Imports

import React from "react"
import ImageGallery from "../components/landingImageGallery"
import TopMenu from "../components/topMenu"
import AnimatedText from "../components/animatedText"
import Footer from "../components/footer"

import "../styles/index.scss"

export default class landingpage extends React.Component {

  render() {

    return (
      <div>

        <TopMenu></TopMenu>

        <header>

          <h1>
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="MARVIN" />
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="SCHWAIBOLD" />
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="GERMAN" />
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="DESIGNER IN NYC" />
          </h1>

          <h2 className="subTitle">
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="EXPLORE ALL MY RECENT" />
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="PROJECTS BELOW. CHECK OUT THE ARCHIVE" />
            <AnimatedText shouldRotate={true} animateOnLoad={true} text="AND ABOUT PAGE FOR MORE WORK." />
          </h2>

        </header>

        <ImageGallery></ImageGallery>

        <div className="backgroundTransition"></div>

        <Footer></Footer>

      </div>
    )

  }

  componentDidMount() {

    // dÃ©termine si la loop doit se faire ou non, se dÃ©sactive au unmount du component
    this.shouldLoop = true

    // rÃ©cupÃ¨re toutes les cartes d'oeuvres
    let oeuvres = document.querySelectorAll(".imageWrapper");
    // rÃ©cupÃ¨re les titres Ã  animer au scroll
    let titleToAnimateOnScroll = document.querySelectorAll("h1 > .animatedText, h2.subTitle > .animatedText, h2.subTitle, footer .animatedText")

    // enregistre la hauteur de l'Ã©cran
    let screenHeight = window.innerHeight;
    // dÃ©finit la loop
    const loop = () => {
      // ne des choses que si le menu est fermÃ©
      if (!document.querySelector(".menuIcon").classList.contains("opened")) {

        // enregistre la position du haut de l'Ã©cran dans le document
        let screenTopPos = window.pageYOffset
        // enregistre la position du bas de l'Ã©cran dans le document
        let screenBottomPos = screenTopPos + screenHeight

        // parcours toutes les cartes d'oeuvres
        oeuvres.forEach((oeuvre) => {
          // rÃ©cupÃ¨re la position en y de cette oeuvre
          let oeuvrePos = screenTopPos + oeuvre.getBoundingClientRect().top
          // rÃ©cupÃ¨re la hauteur de cette oeuvre
          let oeuvreHeight = oeuvre.getBoundingClientRect().height
          // est-ce que l'oeuvre est dans le viewport ?
          let isVisible = !((oeuvrePos + oeuvreHeight) < screenTopPos || oeuvrePos > screenBottomPos)
          if (isVisible)
            oeuvre.classList.remove("closed")
          else
            oeuvre.classList.add("closed")
        })

        // parcours tous les titres
        titleToAnimateOnScroll.forEach((el) => {
          // rÃ©cupÃ¨re la position en y de cet Ã©lÃ©ment
          let elPos = screenTopPos + el.getBoundingClientRect().top;
          // rÃ©cupÃ¨re la hauteur de cet Ã©lÃ©ment
          let elHeight = el.getBoundingClientRect().height;
          // est-ce que l'Ã©lÃ©ment est visible dans le viewport ?
          let isVisible = !((elPos + elHeight) < screenTopPos || elPos > screenBottomPos)

          if (isVisible)
            el.classList.add("animated")
          else
            el.classList.remove("animated")

        })

      }

      // relance la loop si il faut
      if (this.shouldLoop)
        requestAnimationFrame(loop)

    }

    // lance la loop
    loop();

  }

  // lorqu'on quitte la page
  componentWillUnmount() {
    // arrÃªte la loop
    this.shouldLoop = false;
  }


}